// Animate on scroll directive
export default {
  inserted(el, binding) {
    const animationClass = binding.value || 'animate__fadeIn';
    
    // Add animate.css base class
    el.classList.add('animate__animated');
    
    // Initially hide the element
    el.style.opacity = '0';
    
    // Create intersection observer
    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          // Element is visible, trigger animation
          entry.target.style.opacity = '1';
          entry.target.classList.add(animationClass);
          
          // Stop observing once animation is triggered
          observer.unobserve(entry.target);
        }
      });
    }, {
      threshold: 0.1, // Trigger when 10% of element is visible
      rootMargin: '0px 0px -50px 0px' // Trigger slightly before element comes into view
    });
    
    // Start observing the element
    observer.observe(el);
    
    // Store observer on element for cleanup
    el._observer = observer;
  },
  
  unbind(el) {
    // Clean up observer when directive is unbound
    if (el._observer) {
      el._observer.disconnect();
      delete el._observer;
    }
  }
};
