import Vue from 'vue';
import VueRouter from 'vue-router';
import PipelineForm from '@/components/PipelineForm.vue';
import ReportsView from '@/components/Reports.vue';

Vue.use(VueRouter);

export default new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'PipelineForm',
      component: PipelineForm
    },
    {
      path: '/reports',
      name: 'Reports',
      component: ReportsView
    }
  ]
});
