.action-button {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: var(--border-radius);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-top: 1rem;
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.2);
  font-family: var(--font-family-primary);
}

.action-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(37, 99, 235, 0.3);
}

.action-button i {
  font-size: 1.125rem;
}

/* Additional Responsive Design for Results Section */
@media (max-width: 768px) {
  .results-section {
    padding: 2.5rem 0;
  }
  
  .results-title {
    font-size: 2rem;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .stat-card {
    padding: 1.5rem;
  }
  
  .metrics-section {
    padding: 2rem;
  }
  
  .metrics-grid {
    grid-template-columns: 1fr;
  }
  
  .section-title {
    font-size: 1.5rem;
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .chart-container {
    padding: 1.5rem;
    min-height: 300px;
  }
  
  .action-card {
    padding: 2rem;
  }
}

@media (max-width: 480px) {
  .results-section {
    padding: 2rem 0;
  }
  
  .results-title {
    font-size: 1.75rem;
  }
  
  .stat-card {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }
  
  .stat-value {
    font-size: 2rem;
  }
  
  .metrics-section {
    padding: 1.5rem;
  }
  
  .metric-card {
    padding: 1.25rem;
  }
  
  .section-title {
    font-size: 1.25rem;
  }
  
  .chart-container {
    padding: 1rem;
    min-height: 250px;
  }
  
  .action-card {
    padding: 1.5rem;
  }
  
  .action-button {
    width: 100%;
    justify-content: center;
  }
}
</style>

<script>
import axios from 'axios';
import Chart from 'chart.js/auto';
import { getCurrentToken, updateTokenFromPipeline } from '@/utils/tokenManager';

export default {
  data() {
    return {
      isLoading: false,
      showResults: false,        formData: {
        condition: 'Dry Eye',
        startDate: '2023-01-01',
        endDate: 'MAX',
        pubmedStart: '2023/01/01',
        pubmedEnd: '2025/01/01',
        competitors: 'AbbVie, Novartis, Oyster Point, Eyenovia, Bausch',
        retries: 3 // Hidden from UI but still used in API
      },results: {
        trials_fetched: 0,
        trials_filtered: 0,
        pubmed_count: 0,
        message: '',
        publication_metrics: []
      },
      chart: null,
      userToken: null, // Add user token to component data
      errorMessage: '' // Add error message for validation
    };    },
    computed: {
      cardData() {
        return [
          {
            title: 'Trials Fetched',
            value: this.results.trials_fetched,
            icon: 'fas fa-microscope'
          },
          {
            title: 'Trials Filtered', 
            value: this.results.trials_filtered,
            icon: 'fas fa-filter'
          },
          {
            title: 'PubMed Articles',
            value: this.results.pubmed_count,
            icon: 'fas fa-book-open'
          }
        ];
      }
    },
    methods: {
      validateDates() {
        // Extract year from start date
        const startYear = new Date(this.formData.startDate).getFullYear();
        
        // Extract year from pubmed start date (format: YYYY/MM/DD)
        const pubmedStartYear = parseInt(this.formData.pubmedStart.split('/')[0]);
        
        if (startYear <= 2022 || pubmedStartYear <= 2022) {
          this.errorMessage = 'Years 2022 and earlier are not allowed in demo version. Please use 2023 or later.';
          return false;
        }
        
        this.errorMessage = '';
        return true;
      },
      
      // Add a new method to deduplicate publication metrics
      deduplicateMetrics(metrics) {
        const uniqueMap = new Map();
        
        // Use Map to keep only unique entries by competitor
        metrics.forEach(metric => {
          uniqueMap.set(metric.competitor, metric);
        });
        
        // Convert Map values back to array
        return Array.from(uniqueMap.values());
      },
      
      async runPipeline() {
        // Validate dates before proceeding
        if (!this.validateDates()) {
          return;
        }
        
        this.isLoading = true;
        this.showResults = false;
        
        // Get current token from storage (don't create new one)
        this.userToken = getCurrentToken();
        
        try {
          const payload = {
            condition: this.formData.condition,
            start_date: this.formData.startDate,
            end_date: this.formData.endDate,
            competitors: this.formData.competitors.split(',').map(c => c.trim()),
            retries: this.formData.retries,
            pubmed_start: this.formData.pubmedStart,
            pubmed_end: this.formData.pubmedEnd
          };
            // Only include user_token if it exists
          if (this.userToken) {
            payload.user_token = this.userToken;
          }
          
          const apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
          const response = await axios.post(`${apiBaseUrl}/pipeline-full`, payload);
          
          // Get the response data
          const responseData = response.data;
          
          // Deduplicate the publication metrics
          if (responseData.publication_metrics && responseData.publication_metrics.length > 0) {
            responseData.publication_metrics = this.deduplicateMetrics(responseData.publication_metrics);
          }
          
          this.results = responseData;
          
          // Store the user token from response if provided
          if (response.data.user_token) {
            this.userToken = response.data.user_token;
            // Save the token in browser storage
            updateTokenFromPipeline(this.userToken);
          }
          
          this.$nextTick(() => this.updateChart());
          this.showResults = true;
        } catch (err) {
          alert("Pipeline request failed.");
          console.error('Error:', err);
        } finally {
          this.isLoading = false;
        }
      },
      updateChart() {
        // Use nextTick to ensure the DOM is fully updated
        this.$nextTick(() => {
          if (!this.$refs.competitorsChart) {
            console.error('Chart canvas ref not found');
            return;
          }

          const ctx = this.$refs.competitorsChart.getContext('2d');
        
          // Destroy existing chart if it exists
          if (this.chart) {
            this.chart.destroy();
          }

          // Check if we have data to display
          if (!this.results.publication_metrics || this.results.publication_metrics.length === 0) {
            console.warn('No publication metrics data available for chart');
            return;
          }

          try {
            this.chart = new Chart(ctx, {
              type: 'bar',
              data: {
                labels: this.results.publication_metrics.map(m => m.competitor),
                datasets: [{
                  label: 'Publications',
                  data: this.results.publication_metrics.map(m => m.count),
                  backgroundColor: [
                    'rgba(37, 99, 235, 0.6)',
                    'rgba(14, 165, 233, 0.6)',
                    'rgba(59, 130, 246, 0.6)',
                    'rgba(96, 165, 250, 0.6)',
                    'rgba(147, 197, 253, 0.6)'
                  ],
                  borderColor: [
                    'rgba(37, 99, 235, 1)',
                    'rgba(14, 165, 233, 1)',
                    'rgba(59, 130, 246, 1)',
                    'rgba(96, 165, 250, 1)',
                    'rgba(147, 197, 253, 1)'
                  ],
                  borderWidth: 2,
                  borderRadius: 4
                }]
              },
              options: {
                responsive: true,
                maintainAspectRatio: false,
                plugins: {
                  title: {
                    display: true,
                    text: 'Publications by Competitor',
                    font: {
                      size: 16,
                      weight: 'bold'
                    },
                    color: '#1e293b'
                  },
                  legend: {
                    display: false
                  },
                  tooltip: {
                    backgroundColor: 'rgba(30, 41, 59, 0.9)',
                    titleColor: '#ffffff',
                    bodyColor: '#ffffff',
                    borderColor: '#2563eb',
                    borderWidth: 1,
                    callbacks: {
                      label: function(context) {
                        return `Publications: ${context.parsed.y}`;
                      }
                    }
                  }
                },
                scales: {
                  y: {
                    beginAtZero: true,
                    ticks: {
                      stepSize: 1,
                      color: '#64748b'
                    },
                    grid: {
                      color: '#e2e8f0'
                    }
                  },
                  x: {
                    ticks: {
                      color: '#64748b',
                      maxRotation: 45
                    },
                    grid: {
                      display: false
                    }
                  }
                },
                animation: {
                  duration: 1000,
                  easing: 'easeOutQuart'
                }
              }
            });
          } catch (error) {
            console.error('Error creating chart:', error);
          }
        });
      },
      calculatePercentage(count) {
        const maxCount = Math.max(...this.results.publication_metrics.map(m => m.count));
        return (count / maxCount) * 100;
      },
      navigateToReports() {
        this.$router.push({ name: 'Reports' });
      }
    },
    beforeDestroy() {
      if (this.chart) {
        this.chart.destroy();
      }
    }
  }
</script>
