  font-size: 1em;
}

.summary-body strong {
  color: var(--primary-color);
  font-weight: 600;
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.1), rgba(76, 201, 240, 0.1));
  padding: 2px 6px;
  border-radius: 4px;
  display: inline-block;
  margin: 2px 0;
}

.summary-body h3 {
  color: var(--dark-color);
  font-size: 1.2rem;
  font-weight: 600;
  margin: 1.5em 0 0.8em 0;
  padding-bottom: 0.5em;
  border-bottom: 2px solid rgba(37, 99, 235, 0.2);
}

.summary-body h4 {
  color: var(--secondary-color);
  font-size: 1.1rem;
  font-weight: 600;
  margin: 1.2em 0 0.6em 0;
}

/* Company-specific styling */
.summary-body li:has(strong:contains("AbbVie")) {
  background: linear-gradient(135deg, rgba(255, 87, 51, 0.05), rgba(255, 87, 51, 0.02));
  border-left: 3px solid #ff5733;
  padding: 12px 15px;
  border-radius: 8px;
  margin: 10px 0;
}

.summary-body li:has(strong:contains("Novartis")) {
  background: linear-gradient(135deg, rgba(0, 102, 204, 0.05), rgba(0, 102, 204, 0.02));
  border-left: 3px solid #0066cc;
  padding: 12px 15px;
  border-radius: 8px;
  margin: 10px 0;
}

.summary-body li:has(strong:contains("Oyster Point")) {
  background: linear-gradient(135deg, rgba(34, 139, 34, 0.05), rgba(34, 139, 34, 0.02));
  border-left: 3px solid #228b22;
  padding: 12px 15px;
  border-radius: 8px;
  margin: 10px 0;
}

.summary-body li:has(strong:contains("Bausch")) {
  background: linear-gradient(135deg, rgba(138, 43, 226, 0.05), rgba(138, 43, 226, 0.02));
  border-left: 3px solid #8a2be2;
  padding: 12px 15px;
  border-radius: 8px;
  margin: 10px 0;
}

.summary-body li:has(strong:contains("Eyenovia")) {
  background: linear-gradient(135deg, rgba(255, 140, 0, 0.05), rgba(255, 140, 0, 0.02));
  border-left: 3px solid #ff8c00;
  padding: 12px 15px;
  border-radius: 8px;
  margin: 10px 0;
}

/* Mechanism of Action and Clinical Endpoints sections */
.summary-body > ul > li:first-of-type {
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.08), rgba(76, 201, 240, 0.05));
  border: 1px solid rgba(37, 99, 235, 0.2);
  border-radius: 12px;
  padding: 20px;
  margin: 20px 0;
}

.summary-body > ul > li:nth-of-type(2) {
  background: linear-gradient(135deg, rgba(5, 150, 105, 0.08), rgba(16, 185, 129, 0.05));
  border: 1px solid rgba(5, 150, 105, 0.2);
  border-radius: 12px;
  padding: 20px;
  margin: 20px 0;
}

/* Special styling for technical terms */
.summary-body em {
  font-style: italic;
  color: var(--gray-color);
  background: rgba(100, 116, 139, 0.1);
  padding: 1px 4px;
  border-radius: 3px;
  font-size: 0.95em;
}

/* Code-like styling for drug names and abbreviations */
.summary-body code {
  background: rgba(37, 99, 235, 0.1);
  color: var(--primary-color);
  padding: 2px 6px;
  border-radius: 4px;
  font-family: 'Courier New', monospace;
  font-size: 0.9em;
  font-weight: 500;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .summary-body {
    padding: 15px;
    line-height: 1.6;
  }
  
  .summary-body p {
    font-size: 0.95rem;
    text-align: left;
  }
  
  .summary-body ul {
    margin-left: 1em;
  }
  
  .summary-body li {
    padding-left: 1em;
  }
  
  .summary-body ul ul {
    padding-left: 0.8em;
  }
  
  .summary-body strong {
    display: inline;
    padding: 1px 4px;
  }
}

/* Print styles */
@media print {
  .summary-body {
    color: black !important;
    background: white !important;
  }
  
  .summary-body strong {
    background: none !important;
    color: black !important;
    font-weight: bold;
  }
  
  .summary-body li::before {
    color: black !important;
  }
}

</style>
  
<script>
import axios from 'axios';
import { getCurrentToken } from '@/utils/tokenManager';
import TokenUpdater from '@/components/TokenUpdater.vue';

export default {
  name: 'ReportsView',
  components: {
    TokenUpdater
  },
  data() {
    return {
      // Data storage
      trials: [],
      publications: [],
      matchedResults: [],
      allTrialsCsv: [],
      
      // UI state
      loading: {
        initial: true,
        trials: false,
        publications: false,
        matched: false
      },
      activeTab: 'trials',
      currentPage: 1,
      itemsPerPage: 10,
      sortField: null,
      sortOrder: 'asc',
      
      // Search queries for each tab
      trialsSearchQuery: '',
      pubsSearchQuery: '',
      matchedSearchQuery: '',
      
      // Filters for each tab
      trialsFilters: {
        phase: '',
        status: '',
      },
      pubsFilters: {
        pubYear: '',
        journalType: '',
      },
      matchedFilters: {
        similarityThreshold: '0',
        trialPhase: '',
      },
      
      // Modal state
      modalVisible: false,
      selectedTrial: null,
      selectedPublication: null,
      selectedMatch: null,
      matchTrialDetails: null,
      matchPubDetails: null,
      pubDetails: {},
      
      // Debounce timers
      trialsSearchTimer: null,
      pubsSearchTimer: null,
      matchedSearchTimer: null,
      
      // Tabs configuration
      tabs: [
        { id: 'trials', name: 'Clinical Trials', icon: 'fas fa-flask' },
        { id: 'publications', name: 'PubMed Publications', icon: 'fas fa-book-open' },
        { id: 'matched', name: 'Matched Results', icon: 'fas fa-link' }
      ],
      
      // AI summary state
      aiSummary: '',
      aiLoading: false,
      aiError: ''
    }
  },
  created() {
    this.loadData();
  },
  computed: {
    // Dashboard statistics
    dashboardStats() {
      return [
        { 
          label: 'Clinical Trials', 
          value: this.trials.length,
          icon: 'fas fa-flask'
        },
        { 
          label: 'Publications', 
          value: this.publications.length,
          icon: 'fas fa-book-open'
        },
        { 
          label: 'Matched Results', 
          value: this.matchedResults.length,
          icon: 'fas fa-link'
        },
        { 
          label: 'Sponsors', 
          value: this.uniqueSponsors,
          icon: 'fas fa-building'
        }
      ];
    },
    
    // Unique sponsors count
    uniqueSponsors() {
      const sponsors = new Set();
      this.trials.forEach(trial => {
        if (trial.SponsorName) sponsors.add(trial.SponsorName);
      });
      return sponsors.size;
    },
    
    // Filtered trials based on search and filters
    filteredTrials() {
      let result = [...this.trials];
      
      // Apply search
      if (this.trialsSearchQuery) {
        const query = this.trialsSearchQuery.toLowerCase();
        result = result.filter(trial => 
          (trial.BriefTitle && trial.BriefTitle.toLowerCase().includes(query)) ||
          (trial.NCTId && trial.NCTId.toLowerCase().includes(query)) ||
          (trial.SponsorName && trial.SponsorName.toLowerCase().includes(query)) ||
          (trial.Condition && trial.Condition.toLowerCase().includes(query))
        );
      }
      
      // Apply filters
      if (this.trialsFilters.phase) {
        result = result.filter(trial => trial.Phase === this.trialsFilters.phase);
      }
      
      if (this.trialsFilters.status) {
        result = result.filter(trial => trial.OverallStatus === this.trialsFilters.status);
      }
      
      // Apply sorting
      if (this.sortField) {
        result.sort((a, b) => {
          const valA = a[this.sortField] || '';
          const valB = b[this.sortField] || '';
          
          if (typeof valA === 'string' && typeof valB === 'string') {
            return this.sortOrder === 'asc' 
              ? valA.localeCompare(valB) 
              : valB.localeCompare(valA);
          } else {
            return this.sortOrder === 'asc' 
              ? valA - valB 
              : valB - valA;
          }
        });
      }
      
      return result;
    },
    
    // Filtered publications based on search and filters
    filteredPublications() {
      let result = [...this.publications];
      
      // Apply search
      if (this.pubsSearchQuery) {
        const query = this.pubsSearchQuery.toLowerCase();
        result = result.filter(pub => 
          (pub.TITLE && pub.TITLE.toLowerCase().includes(query)) ||
          (pub.PMID && pub.PMID.includes(query)) ||
          (pub.ABSTRACT && pub.ABSTRACT.toLowerCase().includes(query))
        );
      }
      
      // Apply filters
      if (this.pubsFilters.pubYear) {
        if (this.pubsFilters.pubYear === 'older') {
          result = result.filter(pub => {
            const pubYear = pub.PUBDATE ? parseInt(pub.PUBDATE.substring(0, 4)) : 0;
            return pubYear < 2020;
          });
        } else {
          const yearFilter = this.pubsFilters.pubYear;
          result = result.filter(pub => {
            const pubYear = pub.PUBDATE ? pub.PUBDATE.substring(0, 4) : '';
            return pubYear === yearFilter;
          });
        }
      }
      
      if (this.pubsFilters.journalType) {
        const journalType = this.pubsFilters.journalType.toLowerCase();
        result = result.filter(pub => 
          pub.JOURNAL && pub.JOURNAL.toLowerCase().includes(journalType)
        );
      }
      
      return result;
    },
    
    // Filtered matched results based on search and filters
    filteredMatched() {
      let result = [...this.matchedResults];
      
      // Apply search
      if (this.matchedSearchQuery) {
        const query = this.matchedSearchQuery.toLowerCase();
        result = result.filter(match => 
          (match.NCTId && match.NCTId.toLowerCase().includes(query)) ||
          (match.TrialTitle && match.TrialTitle.toLowerCase().includes(query)) ||
          (match.BestPubPMID && match.BestPubPMID.includes(query))
        );
      }
      
      // Apply similarity threshold filter
      if (this.matchedFilters.similarityThreshold && parseFloat(this.matchedFilters.similarityThreshold) > 0) {
        const threshold = parseFloat(this.matchedFilters.similarityThreshold);
        result = result.filter(match => 
          parseFloat(match.SimilarityScore) >= threshold
        );
      }
      
      // Apply trial phase filter if selected
      if (this.matchedFilters.trialPhase) {
        const phaseFilter = this.matchedFilters.trialPhase;
        // We need to check the actual trial data
        result = result.filter(match => {
          const trial = this.trials.find(t => t.NCTId === match.NCTId);
          return trial && trial.Phase === phaseFilter;
        });
      }
      
      return result;
    },
    
    // Paginated data for current tab
    paginatedTrials() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredTrials.slice(start, end);
    },
    
    paginatedPublications() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredPublications.slice(start, end);
    },
    
    paginatedMatched() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredMatched.slice(start, end);
    },
    
    // Calculate total pages based on active tab
    totalPages() {
      let totalItems = 0;
      
      if (this.activeTab === 'trials') {
        totalItems = this.filteredTrials.length;
      } else if (this.activeTab === 'publications') {
        totalItems = this.filteredPublications.length;
      } else if (this.activeTab === 'matched') {
        totalItems = this.filteredMatched.length;
      }
      
      return Math.max(1, Math.ceil(totalItems / this.itemsPerPage));
    },
    
    // Get associated publications for the selected trial
    trialPublications() {
      if (!this.selectedTrial || !this.selectedTrial.NCTId) return [];
      
      return this.matchedResults.filter(match => 
        match.NCTId === this.selectedTrial.NCTId
      );
    },
    
    // Get associated trials for the selected publication
    publicationTrials() {
      if (!this.selectedPublication || !this.selectedPublication.PMID) return [];
      
      return this.matchedResults.filter(match => 
        match.BestPubPMID === this.selectedPublication.PMID
      );
    }
  },
  methods: {    async loadData() {      try {
        this.loading.initial = true;
        
        // Get the user token from storage
        const userToken = getCurrentToken();
        
        // Get API base URL from environment variable
        const apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
        
        // Load all data in parallel with user token parameter
        const [trialsResponse, publicationsResponse, matchedResponse, allTrialsResponse] = await Promise.all([
          axios.get(`${apiBaseUrl}/get-filtered-trials`, { params: { user_token: userToken } }),
          axios.get(`${apiBaseUrl}/get-pubmed-summaries`, { params: { user_token: userToken } }),
          axios.get(`${apiBaseUrl}/get-matched-results`, { params: { user_token: userToken } }),
          axios.get(`${apiBaseUrl}/get-trial-csv`, { params: { user_token: userToken } })
        ]);
        
        // Store the data
        this.trials = trialsResponse.data;
        this.publications = publicationsResponse.data;
        this.matchedResults = matchedResponse.data;
        this.allTrialsCsv = allTrialsResponse.data;
        
        // Create a lookup of publications by PMID for quick access
        this.publications.forEach(pub => {
          this.pubDetails[pub.PMID] = pub;
        });
        
        this.loading.initial = false;
      } catch (error) {
        console.error('Error loading data:', error);
        this.loading.initial = false;
        alert('Failed to load data. Please check the console for details.');
      }
    },
    
    // Tab management
    setActiveTab(tabId) {
      this.activeTab = tabId;
      this.currentPage = 1; // Reset to first page on tab change
      // No longer clear search on tab change - each tab has its own search state
      this.sortField = null; // Reset sorting
      this.sortOrder = 'asc';
    },
    
    // Pagination
    changePage(direction) {
      const newPage = this.currentPage + direction;
      if (newPage >= 1 && newPage <= this.totalPages) {
        this.currentPage = newPage;
        window.scrollTo(0, 0);
      }
    },
    
    // Sorting
    sortData(dataType, field) {
      if (this.sortField === field) {
        // Toggle sort order if already sorting by this field
        this.sortOrder = this.sortOrder === 'asc' ? 'desc' : 'asc';
      } else {
        // Set new sort field and default to ascending
        this.sortField = field;
        this.sortOrder = 'asc';
      }
    },
    
    // Get sort icon class based on current sort state
    getSortIconClass(field) {
      if (this.sortField !== field) return 'fas fa-sort';
      return this.sortOrder === 'asc' ? 'fas fa-sort-up' : 'fas fa-sort-down';
    },
    
    // Search debouncing for each tab
    debounceTrialsSearch() {
      clearTimeout(this.trialsSearchTimer);
      this.trialsSearchTimer = setTimeout(() => {
        this.currentPage = 1; // Reset to first page on search
      }, 300);
    },
    
    debouncePublicationsSearch() {
      clearTimeout(this.pubsSearchTimer);
      this.pubsSearchTimer = setTimeout(() => {
        this.currentPage = 1; // Reset to first page on search
      }, 300);
    },
    
    debounceMatchedSearch() {
      clearTimeout(this.matchedSearchTimer);
      this.matchedSearchTimer = setTimeout(() => {
        this.currentPage = 1; // Reset to first page on search
      }, 300);
    },
    
    // Modal management for details views
    showTrialDetails(trial) {
      this.selectedTrial = trial;
      this.selectedPublication = null;
      this.selectedMatch = null;
      this.modalVisible = true;
    },
    
    showPublicationDetails(publication) {
      this.selectedPublication = publication;
      this.selectedTrial = null;
      this.selectedMatch = null;
      this.modalVisible = true;
    },
    
    async showMatchDetails(match) {
      this.selectedMatch = match;
      this.selectedTrial = null;
      this.selectedPublication = null;
      this.modalVisible = true;
      
      // Find trial details
      this.matchTrialDetails = this.trials.find(t => t.NCTId === match.NCTId);
      
      // Fetch publication details if not already cached
      if (!this.pubDetails[match.BestPubPMID]) {
        try {
          const apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
          const response = await axios.get(`${apiBaseUrl}/get-pubmed-summaries-by-PMID/${match.BestPubPMID}`);
          this.pubDetails[match.BestPubPMID] = response.data;
          this.matchPubDetails = response.data;
        } catch (error) {
          console.error('Error fetching publication details:', error);
        }
      } else {
        this.matchPubDetails = this.pubDetails[match.BestPubPMID];
      }
    },
    
    async fetchAndShowPubDetails(pmid) {
      try {
        // Show loading state
        this.loading.publications = true;
        
        // Check if we already have the publication details cached
        if (this.pubDetails[pmid]) {
          this.showPublicationDetails(this.pubDetails[pmid]);
        } else {
          // Fetch the publication details from the API
          const apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
          const response = await axios.get(`${apiBaseUrl}/get-pubmed-summaries-by-PMID/${pmid}`);
          
          // Add to cache
          this.pubDetails[pmid] = response.data;
          
          // Show the publication details
          this.showPublicationDetails(response.data);
        }
      } catch (error) {
        console.error(`Error fetching publication details for PMID ${pmid}:`, error);
        alert(`Failed to load publication details for PMID ${pmid}`);
      } finally {
        this.loading.publications = false;
      }
    },
    
    showTrialById(nctId) {
      const trial = this.trials.find(t => t.NCTId === nctId);
      if (trial) {
        this.showTrialDetails(trial);
      }
    },
    
    closeModal() {
      this.modalVisible = false;
      this.selectedTrial = null;
      this.selectedPublication = null;
      this.selectedMatch = null;
      this.matchTrialDetails = null;
      this.matchPubDetails = null;
    },
    
    // Helper functions
    truncateText(text, maxLength) {
      if (!text) return '';
      return text.length > maxLength ? text.substring(0, maxLength) + '...' : text;
    },
    
    getPhaseClass(phase) {
      if (!phase) return 'na';
      if (phase.includes('1')) return 'phase1';
      if (phase.includes('2')) return 'phase2';
      if (phase.includes('3')) return 'phase3';
      if (phase.includes('4')) return 'phase4';
      return 'na';
    },
    
    getStatusClass(status) {
      if (!status) return 'unknown';
      const s = status.toLowerCase();
      if (s.includes('recruiting')) return 'recruiting';
      if (s.includes('completed')) return 'completed';
      if (s.includes('active')) return 'active';
      if (s.includes('withdrawn')) return 'withdrawn';
      return 'unknown';
    },
      async getAISummary() {
      if (this.aiLoading) return;
      this.aiLoading = true;
      this.aiError = '';
      this.aiSummary = '';      try {
        // Get the user token from storage
        const userToken = getCurrentToken();
        
        const apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
        const resp = await axios.get(`${apiBaseUrl}/ai-summarize`, {
          params: { user_token: userToken }
        });
        // assume the API returns raw HTML string
        resp.data=resp.data.replace(/```html/g, '');
        this.aiSummary = resp.data.replace(/```/g, '');
      } catch (err) {
        console.error(err);
        this.aiError = 'Failed to load AI summary. Try again later.';
      } finally {
        this.aiLoading = false;
      }
    }
  }
}
</script>
