/**
 * Token Management Utility for Medical Research Pipeline
 * Handles user token generation, storage, and retrieval
 */

const TOKEN_STORAGE_KEY = 'medical_research_user_token';
const TOKEN_FILE = 'user.txt';

/**
 * Generate a unique token based on timestamp and random ID
 */
function generateToken() {
  const timestamp = new Date().toISOString().replace(/[-:T.Z]/g, '').substring(0, 14);
  const randomId = Math.random().toString(36).substring(2, 8);
  return `${timestamp}_${randomId}`;
}

/**
 * Get token from cookies
 */
function getTokenFromCookie() {
  const name = TOKEN_STORAGE_KEY + "=";
  const decodedCookie = decodeURIComponent(document.cookie);
  const ca = decodedCookie.split(';');
  
  for (let i = 0; i < ca.length; i++) {
    let c = ca[i];
    while (c.charAt(0) === ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) === 0) {
      return c.substring(name.length, c.length);
    }
  }
  return null;
}

/**
 * Set token in cookies (30 days expiration)
 */
function setTokenInCookie(token) {
  const d = new Date();
  d.setTime(d.getTime() + (30 * 24 * 60 * 60 * 1000)); // 30 days
  const expires = "expires=" + d.toUTCString();
  document.cookie = `${TOKEN_STORAGE_KEY}=${token};${expires};path=/`;
}

/**
 * Get token from localStorage
 */
function getTokenFromLocalStorage() {
  return localStorage.getItem(TOKEN_STORAGE_KEY);
}

/**
 * Set token in localStorage
 */
function setTokenInLocalStorage(token) {
  localStorage.setItem(TOKEN_STORAGE_KEY, token);
}

/**
 * Save token to user.txt file (for demonstration purposes)
 * In a real application, this would be handled server-side
 */
async function saveTokenToFile(token) {
  try {
    // This is a client-side implementation for demonstration
    // In production, this would be an API call to save the token server-side
    const blob = new Blob([token], { type: 'text/plain' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.style.display = 'none';
    a.href = url;
    a.download = TOKEN_FILE;
    document.body.appendChild(a);
    
    // Note: We're not actually triggering the download automatically
    // as this would be annoying for users. Instead, we just save to browser storage
    document.body.removeChild(a);
    window.URL.revokeObjectURL(url);
    
    console.log(`Token saved to browser storage: ${token}`);
    return true;
  } catch (error) {
    console.error('Error saving token to file:', error);
    return false;
  }
}

/**
 * Get or create user token
 * Priority: cookie > localStorage > generate new
 */
function getOrCreateToken() {
  // First try to get from cookie
  let token = getTokenFromCookie();
  
  if (!token) {
    // Try localStorage
    token = getTokenFromLocalStorage();
    
    if (token) {
      // If found in localStorage, save to cookie as well
      setTokenInCookie(token);
    }
  }
  
  if (!token) {
    // Generate new token
    token = generateToken();
    
    // Save to both cookie and localStorage
    setTokenInCookie(token);
    setTokenInLocalStorage(token);
    
    // Save to file (demonstration)
    saveTokenToFile(token);
    
    console.log('Generated new user token:', token);
  } else {
    console.log('Using existing user token:', token);
  }
  
  return token;
}

/**
 * Clear token from all storage locations
 */
function clearToken() {
  // Clear cookie
  document.cookie = `${TOKEN_STORAGE_KEY}=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;`;
  
  // Clear localStorage
  localStorage.removeItem(TOKEN_STORAGE_KEY);
  
  console.log('User token cleared');
}

/**
 * Update token from pipeline response
 * @param {string} token - The token received from the pipeline response
 */
function updateTokenFromPipeline(token) {
  if (!token) return false;
  
  setTokenInCookie(token);
  setTokenInLocalStorage(token);
  console.log('Updated user token from pipeline response:', token);
  return true;
}

/**
 * Get current token without creating a new one
 */
function getCurrentToken() {
  return getTokenFromCookie() || getTokenFromLocalStorage();
}

export {
  generateToken,
  getOrCreateToken,
  getCurrentToken,
  clearToken,
  setTokenInCookie,
  setTokenInLocalStorage,
  getTokenFromCookie,
  getTokenFromLocalStorage,
  updateTokenFromPipeline
};
